.main-result-flex {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  justify-content: space-between;
}

.flex-box-left {
  width: 35%;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
}

.flex-box-right {
  width: 60%;
}

.extra-results-flex {
  display: flex;
  list-style-type: none;
  margin: 30px 0;
  justify-content: space-between;
}

.flex-box {
  padding: 0 30px;
}

#destinationImage {
  width: 100%;
  border-radius: 6px;

}
